        title: 'Tim  Roman Numerals',
        content: 'Tim richt zijn huis opnieuw in. Hij wil ooit een digitale kalender maken met Romeinse cijfers om aan de muur te hangen. Aangezien hij nogal moeite heeft met het converteren van nummers naar Romeinse cijfers wil hij allereerst graag een programma die dat voor hem bouwt.\n          Maak een programma dat elk nummer t/m 2000 om kan zetten in Romeinse cijfers. Tim wil dat dit makkelijk uit breiden is en mag dus niet hardcoded zijn.\n          ',
